<template>
  <div class="qnaView">
    <div class="col-12 col-md-8 head"><h2>묻고 답하기</h2></div>
    <div class="col-12 col-md-8 titlebox">
      <h3 class="title">{{ qna.title }}</h3>
      <h6 class="title">조회수 : {{ qna.viewcnt }}</h6>
      <h6 class="title">작성자 : {{ qna.userid }}</h6>
      <h6 class="title">
        공개여부 : {{ qna.open == "Y" ? "공개" : "비공개" }}
      </h6>
      <h6 class="title">
        작성일 : {{ new Date(qna.wrdate) | moment("YYYY년 MM월 DD일") }}
      </h6>
    </div>
    <div class="row col-12 col-md-8 contentbox">
      <textarea
        class="col-12 form-control"
        readonly
        v-model="qna.content"
      ></textarea>
    </div>
    <div class="col-12 col-md-8 anser" v-if="qna.anser != null">
      <h3 class="title">답변</h3>
      <h6 class="title">작성자 : 관리자</h6>
      <h6 class="title">
        작성일 : {{ new Date(qna.andate) | moment("YYYY년 MM월 DD일") }}
      </h6>
    </div>

    <div class="row col-12 col-md-8 contentbox" v-if="qna.anser != null">
      <textarea
        class="col-12 form-control"
        readonly
        v-model="qna.anser"
      ></textarea>
    </div>

    <br />
    <button
      type="button"
      id="back"
      class="btn btn-primary btn-md"
      @click="backList"
    >
      목록으로
    </button>
    <br /><br />
  </div>
</template>

<script>
export default {
  name: "qnaView",
  data() {
    return { qna: {} };
  },
  methods: {
    update() {
      this.$http
        .get("/pettime/rest/qna/view/" + this.$route.query.uid)
        .then((res) => {
          this.qna = res.data.data[0];
        });
    },
    backList() {
      this.$router.push("qna.vue");
    },
  },
  created() {
    this.update();
  },
};
</script>

<style>
.qnaView {
  margin-top: 5%;
}
.qnaView .titlebox {
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 2% auto;
}

.qnaView .anser {
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 2% auto;
}
.qnaView h2 {
  font-weight: bold;
}
.qnaView .head {
  margin: auto;
}
.qnaView .title {
  margin: auto;
}
.qnaView .contentbox {
  overflow: auto;
  margin: auto;
}
.qnaView #back {
  text-align: center;
  margin: auto;
  display: block;
}

.form-control[readonly] {
  background-color: white;
  opacity: 1;
}
@media (min-width: 767px) {
  .qnaView .contentbox {
    font-size: 20px;
  }
}
</style>
